<template>
  <div>
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        /
        <span>新消息</span>
      </template>
      <template #content>
        <div class="apply" v-if="hasnotread.length">
          <p v-for="item in hasnotread" :key="item.id">
            <router-link :to="`/user/${item.author.loginname}`">
              {{ item.author.loginname }}
            </router-link>

            回复了你的话题
            <router-link :to="`/topic/${item.topic.id}`">
              {{ item.topic.title }}
            </router-link>
          </p>
        </div>
        <div v-else>无消息</div>
      </template>
    </Panel>
    <Panel>
      <template #header> 过往消息 </template>
      <template #content>
        <div class="apply" v-if="hasread.length">
          <p v-for="item in hasread" :key="item.id">
            <router-link :to="`/user/${item.author.loginname}`">
              {{ item.author.loginname }}
            </router-link>
            回复了你的话题
            <router-link :to="`/topic/${item.topic.id}`">
              {{ item.topic.title }}
            </router-link>
          </p>
        </div>
        <div v-else>无过往消息</div>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  created() {
    this.getMessage();
    this.allRead();
  },
  computed: {
    ...mapState({
      hasread: (state) => state.message.has_read_messages,
      hasnotread: (state) => state.message.hasnot_read_messages,
    }),
  },
  methods: {
    ...mapActions(["getMessage", "allRead"]),
  },
};
</script>

